.viewer-container,
.viewer-navbar {
  background-color: #000;
  overflow: hidden;
}
.viewer-canvas,
.viewer-container,
.viewer-footer,
.viewer-player {
  right: 0;
  bottom: 0;
  left: 0;
}
.viewer-button,
.viewer-canvas,
.viewer-container,
.viewer-footer,
.viewer-list,
.viewer-navbar,
.viewer-open,
.viewer-title,
.viewer-toolbar,
.viewer-toolbar > li {
  overflow: hidden;
}
.viewer-close:before,
.viewer-flip-horizontal:before,
.viewer-flip-vertical:before,
.viewer-fullscreen-exit:before,
.viewer-fullscreen:before,
.viewer-next:before,
.viewer-one-to-one:before,
.viewer-play:before,
.viewer-prev:before,
.viewer-reset:before,
.viewer-rotate-left:before,
.viewer-rotate-right:before,
.viewer-zoom-in:before,
.viewer-zoom-out:before {
  font-size: 0;
  line-height: 0;
  display: block;
  width: 20px;
  height: 20px;
  color: transparent;
  background-image: url(data:image/png;base64,);
  background-repeat: no-repeat;
}
.viewer-zoom-in:before {
  content: 'Zoom In';
  background-position: 0 0;
}
.viewer-zoom-out:before {
  content: 'Zoom Out';
  background-position: -20px 0;
}
.viewer-one-to-one:before {
  content: 'One to One';
  background-position: -40px 0;
}
.viewer-reset:before {
  content: 'Reset';
  background-position: -60px 0;
}
.viewer-prev:before {
  content: 'Previous';
  background-position: -80px 0;
}
.viewer-play:before {
  content: 'Play';
  background-position: -100px 0;
}
.viewer-next:before {
  content: 'Next';
  background-position: -120px 0;
}
.viewer-rotate-left:before {
  content: 'Rotate Left';
  background-position: -140px 0;
}
.viewer-rotate-right:before {
  content: 'Rotate Right';
  background-position: -160px 0;
}
.viewer-flip-horizontal:before {
  content: 'Flip Horizontal';
  background-position: -180px 0;
}
.viewer-flip-vertical:before {
  content: 'Flip Vertical';
  background-position: -200px 0;
}
.viewer-fullscreen:before {
  content: 'Enter Full Screen';
  background-position: -220px 0;
}
.viewer-fullscreen-exit:before {
  content: 'Exit Full Screen';
  background-position: -240px 0;
}
.viewer-close:before {
  content: 'Close';
  background-position: -260px 0;
}
.viewer-container {
  font-size: 0;
  line-height: 0;
  position: absolute;
  top: 0;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  background-color: rgba(0, 0, 0, 0.5);
  direction: ltr !important;
  -ms-touch-action: none;
  touch-action: none;
  -webkit-tap-highlight-color: transparent;
  -webkit-touch-callout: none;
}
.viewer-container ::-moz-selection,
.viewer-container::-moz-selection {
  background-color: transparent;
}
.viewer-container ::selection,
.viewer-container::selection {
  background-color: transparent;
}
.viewer-container img {
  display: block;
  width: 100%;
  min-width: 0 !important;
  max-width: none !important;
  height: auto;
  min-height: 0 !important;
  max-height: none !important;
}
.viewer-player,
.viewer-tooltip {
  display: none;
  position: absolute;
}
.viewer-canvas {
  position: absolute;
  top: 0;
}
.viewer-canvas > img {
  width: auto;
  max-width: 90% !important;
  height: auto;
  margin: 15px auto;
}
.viewer-footer {
  position: absolute;
  text-align: center;
}
.viewer-navbar {
  background-color: rgba(0, 0, 0, 0.5);
}
.viewer-list {
  -webkit-box-sizing: content-box;
  -moz-box-sizing: content-box;
  box-sizing: content-box;
  height: 50px;
  margin: 0;
  padding: 1px 0;
}
.viewer-list > li {
  font-size: 0;
  line-height: 0;
  float: left;
  overflow: hidden;
  width: 30px;
  height: 50px;
  cursor: pointer;
  opacity: 0.5;
  color: transparent;
  filter: alpha(opacity=50);
}
.viewer-list > li + li {
  margin-left: 1px;
}
.viewer-list > .viewer-active {
  opacity: 1;
  filter: alpha(opacity=100);
}
.viewer-player {
  top: 0;
  cursor: none;
  background-color: #000;
}
.viewer-player > img {
  position: absolute;
  top: 0;
  left: 0;
}
.viewer-toolbar {
  width: 280px;
  margin: 0 auto 5px;
  padding: 3px 0;
}
.viewer-toolbar > li {
  float: left;
  width: 24px;
  height: 24px;
  cursor: pointer;
  border-radius: 50%;
  background-color: #000;
  background-color: rgba(0, 0, 0, 0.5);
}
.viewer-toolbar > li:hover {
  background-color: #000;
  background-color: rgba(0, 0, 0, 0.8);
}
.viewer-toolbar > li:before {
  margin: 2px;
}
.viewer-toolbar > li + li {
  margin-left: 1px;
}
.viewer-toolbar > .viewer-play {
  width: 30px;
  height: 30px;
  margin-top: -3px;
  margin-bottom: -3px;
}
.viewer-toolbar > .viewer-play:before {
  margin: 5px;
}
.viewer-tooltip {
  font-size: 12px;
  line-height: 20px;
  top: 50%;
  left: 50%;
  width: 50px;
  height: 20px;
  margin-top: -10px;
  margin-left: -25px;
  text-align: center;
  color: #fff;
  border-radius: 10px;
  background-color: #000;
  background-color: rgba(0, 0, 0, 0.8);
}
.viewer-title {
  font-size: 12px;
  line-height: 1;
  display: inline-block;
  max-width: 90%;
  margin: 0 5% 5px;
  white-space: nowrap;
  text-overflow: ellipsis;
  opacity: 0.8;
  color: #ccc;
  filter: alpha(opacity=80);
}
.viewer-title:hover {
  opacity: 1;
  filter: alpha(opacity=100);
}
.viewer-button {
  position: absolute;
  top: -40px;
  right: -40px;
  width: 80px;
  height: 80px;
  cursor: pointer;
  border-radius: 50%;
  background-color: #000;
  background-color: rgba(0, 0, 0, 0.5);
}
.viewer-button:before {
  position: absolute;
  bottom: 15px;
  left: 15px;
}
.viewer-fixed {
  position: fixed;
}
.viewer-show {
  display: block;
}
.viewer-hide {
  display: none;
}
.viewer-invisible {
  visibility: hidden;
}
.viewer-move {
  cursor: move;
  cursor: -webkit-grab;
  cursor: -moz-grab;
  cursor: grab;
}
.viewer-fade {
  opacity: 0;
  filter: alpha(opacity=0);
}
.viewer-in {
  opacity: 1;
  filter: alpha(opacity=100);
}
.viewer-transition {
  -webkit-transition: all 0.3s ease-out;
  -o-transition: all 0.3s ease-out;
  transition: all 0.3s ease-out;
}
@media (max-width: 767px) {
  .viewer-hide-xs-down {
    display: none;
  }
}
@media (max-width: 991px) {
  .viewer-hide-sm-down {
    display: none;
  }
}
@media (max-width: 1199px) {
  .viewer-hide-md-down {
    display: none;
  }
}
